import React, { Component } from 'react'
import './App.css';
import Board from "./Board/Board";
import { data } from "./data"
import { maze1 } from "./maze"
import Header from "./Header/Header"
let d1 = []
let backup = data()
function arrayClone(arr) {
  var i, copy;
  if (Array.isArray(arr)) {
    copy = arr.slice(0);
    for (i = 0; i < copy.length; i++) {
      copy[i] = arrayClone(copy[i]);
    }
    return copy;
  } else if (typeof arr === 'object') {
    throw 'Cannot clone array containing an object!';
  } else {
    return arr;
  }

}


export default class App extends Component {

  constructor() {
    super();
    this.state = {
      d: data(),
      edit: false,
      disabled: false,
      speed: 50,
    }

  }

  paint = (i) => {

    if (d1.length < i) return
    let x = [...this.state.d]


    x[d1[i][0]][d1[i][1]] = 2;

    this.setState({ d: x })
    setTimeout(() => {
      try {
        this.paint(++i)
      } catch (e) {
        this.setState({ disabled: false })
      }
    }, this.state.speed);

  }

  clicked = async () => {
    let that = this;
    if (this.state.d[0][0] != 0) {
      await this.clear()
    }
    this.setState({ disabled: true }, () => {
      if (that.state.d[0][0] == 0) {
        backup = arrayClone(that.state.d)
      }
      d1 = maze1(that.state.d)
      if (d1) {
        that.paint(0)
      } else {
        alert("Path not found")
        this.setState({ disabled: false })
      }
    })
  }

  clear = () => {
    let _data = []
    for (let i = 0; i < this.state.d.length; i++) {
      let row = []
      for (let j = 0; j < this.state.d[0].length; j++) {
        switch (this.state.d[i][j]) {
          case 1: row.push(1); break;
          default: row.push(0);
        }

      }
      _data.push(row)
    }
    this.setState({ d: _data }, () => {
      return
    })
  }

  build = (x, y) => {
    if (this.state.disabled == true) return
    let _d = [...this.state.d]
    _d[x][y] = _d[x][y] == 1 ? 0 : 1
    this.setState({ d: _d })
  }

  render() {
    return (
      <>
        <Header />
        <div className="App">

          < input type="checkBox" name="name" onClick={() => this.setState({ edit: !this.state.edit })} disabled={this.state.disabled} />
          <label>edit</label>
          <button className="btn btn-primary" onClick={this.clicked} disabled={this.state.disabled}> start </button>
          <button className="btn btn-danger" onClick={this.clear} disabled={this.state.disabled}> clear</button>
          <select onChange={(e) => { this.setState({ speed: e.target.value }) }} value={this.state.speed}>
            <option value="1"> fast </option>
            <option value="50"> medium</option>

            <option value="100"> slow </option>
          </select>

          <Board data={this.state.d} build={this.build} running={this.state.edit} />

        </div>

      </>
    )
  }


}

