import React, { Component } from 'react'

export default class Square extends Component {
    constructor() {
        super();
        this.state = {

        }

    }



    clicked = () => {
        // alert(this.props.row, this.props.col)
        if (this.props.running == true) {
            this.props.build(this.props.row, this.props.col)
        }
    }

    render() {
        // let x = Math.floor(Math.random() * 600) + 1

        // let y = Math.floor(Math.random() * 500) + 1

        // let z = Math.floor(Math.random() * 800) + 1

        // // `rgb(${x},${y},${z})`,
        let styles = {};

        switch (this.props.color) {
            case 3:
            case "3": styles = {
                backgroundColor: "white",
            }
                break
            case "0":
            case 0: styles = {
                backgroundColor: "white",
            }
                break
            case 1:
            case "1": styles = {
                backgroundColor: "grey",
            }; break;

            case 2:
            case "2": styles = {
                backgroundColor: "skyblue",
            }
        }



        return (
            <button
                className="b"
                onClick={this.clicked}
                style={styles}
            // key={this.props.keyVal}
            >
                {}
            </button>
        )

    }
}
