import React, { Component } from 'react'
import Square from "./Square";

export default class Board extends Component {
    constructor() {
        super();
        this.state = {

        }

    }

    componentWillReceiveProps() {

    }

    sq2 = (row, data) => {
        return (

            <div className="r">
                {
                    [...Array(data[row].length).keys()].map((n, k) => {
                        return <Square color={data[row][n]} row={row} col={n} build={this.props.build} running={this.props.running} />
                    })
                }
            </div>
        )
    }

    sq = (data) => {
        return (
            <>
                {[...Array(data.length).keys()].map((n, k) => {
                    return this.sq2(n, data)
                })}
            </>

        )
    }

    render() {

        return (
            <>
                < div className="center" >
                    {
                        this.sq(this.props.data)
                    }
                </div >
            </>
        )
    }
}
