export const maze1 = (data) => {
    let solutions = []
    let _data = arrayClone(data)
    solve_maze2(0, 0, _data.length, _data[0].length, [], _data, solutions)
    solutions.sort(function (a, b) {
        // ASC  -> a.length - b.length
        // DESC -> b.length - a.length
        return a.length - b.length;
    });
    return solutions[0]
}

function arrayClone(arr) {

    var i, copy;

    if (Array.isArray(arr)) {
        copy = arr.slice(0);
        for (i = 0; i < copy.length; i++) {
            copy[i] = arrayClone(copy[i]);
        }
        return copy;
    } else if (typeof arr === 'object') {
        throw 'Cannot clone array containing an object!';
    } else {
        return arr;
    }

}

const solve_maze = (x, y, X, Y, path, data, solutions) => {
    if (x >= X || y >= Y) {
        return
    }

    if (x == X - 1 && y == Y - 1) {
        path = [...path, [x, y]]
        solutions.push(path)
        return
    }

    if (x != X - 1) {
        if (data[x + 1][y] != 1) {
            let _path = [...path, [x, y]]
            solve_maze(x + 1, y, X, Y, _path, data, solutions)
        }
    }

    if (y != Y - 1) {
        if (data[x][y + 1] != 1) {
            let _path = [...path, [x, y]]
            solve_maze(x, y + 1, X, Y, _path, data, solutions)
        }
    }

}

const solve_maze2 = (x, y, X, Y, path, data, solutions) => {
    if (x >= X || y >= Y || x < 0 || y < 0) {
        return
    }

    if (x == X - 1 && y == Y - 1) {
        path = [...path, [x, y]]
        solutions.push(path)
        return
    }

    if (x != X - 1) {
        if (data[x + 1][y] < 1) {
            let _path = [...path, [x, y]]
            data[x][y] = 3
            solve_maze2(x + 1, y, X, Y, _path, data, solutions)
        }
    }

    if (y != Y - 1) {
        if (data[x][y + 1] < 1) {
            let _path = [...path, [x, y]]
            data[x][y] = 3
            solve_maze2(x, y + 1, X, Y, _path, data, solutions)
        }
    }

    if (x != 0) {
        if (data[x - 1][y] < 1) {
            let _path = [...path, [x, y]]
            data[x][y] = 3
            solve_maze2(x - 1, y, X, Y, _path, data, solutions)
        }
    }

    if (y != 0) {
        if (data[x][y - 1] < 1) {
            let _path = [...path, [x, y]]
            data[x][y] = 3
            solve_maze2(x, y - 1, X, Y, _path, data, solutions)
        }
    }



}
